<template>
  <div class="result">
    <div class="result_fix">
      <div class="result_img">
        <router-link to="/google">
          <img src="../assets/smallGoogle.png" alt="small google logo"
        /></router-link>
      </div>
      <form class="form_action" @submit.prevent="search">
        <input type="text" class="form_input" v-model="keyword" />
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="18"
          height="18"
          fill="currentColor"
          class="bi bi-search searchIcon"
          viewBox="0 0 18 18"
        >
          <path
            d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"
          />
        </svg>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="16"
          fill="currentColor"
          class="bi bi-x closeIcon"
          viewBox="0 0 16 16"
          @click="closeFunc"
          v-show="this.keyword.length > 1"
        >
          <path
            d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"
          />
        </svg>
      </form>
    </div>
    <show :results="googleResults" />
  </div>
</template>

<script>
import axios from "axios";
import show from "./show.vue";

export default {
  name: "result",
  components: {
    show,
  },
  data() {
    return {
      api_key: "AIzaSyAgYrBroPJnXyP4W_6kYOk8lQcRCpNx7V8",
      cx: "613e0c3f4db0328bc",
      keyword: this.$route.params.query
        ? this.$route.params.query.split("+").join(" ")
        : "",
      googleResults: [],
    };
  },
  methods: {
    closeFunc() {
      this.keyword = "";
    },
    async search() {
      const newQuery = this.keyword.split(" ").join("+");
      this.$router.push({
        name: "result",
        params: { query: newQuery },
      });
      try {
        const res = await axios.get(
          `https://www.googleapis.com/customsearch/v1?key=${this.api_key}&cx=${this.cx}&q=${this.keyword}`
        );
        this.googleResults = res.data.items;
        console.log(this.googleResults);
      } catch (error) {
        console.log(error);
      }
    },
  },
  async created() {
    if (this.$route.params.query) {
      try {
        const res = await axios.get(
          `https://www.googleapis.com/customsearch/v1?key=${this.api_key}&cx=${this.cx}&q=${this.keyword}`
        );
        this.googleResults = res.data.items;
        console.log(this.googleResults);
      } catch (error) {
        console.log(error);
      }
    }
  },
};
</script>

